*{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
header{
  width: 100%;
  height: 70px;
  background-color: #2c698d;
  position: fixed;
  z-index: 10;
}
nav{
  width: 100%;
  height: 100%;
  position: relative;
}
.menu{
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  &__item{
      list-style-type: none;
      color: #272643;
      text-transform: uppercase;
      text-shadow: 2px 2px 4px #bae8e8;
      position: relative;
      font-size: 24px;
  }
  &__link{
      display: flex;
      justify-content: center;
      align-items: center;
  }
  &__item:nth-child(1){
	margin-left: 40px;
}
  &__item:nth-child(2){
      margin-left: 20px;
  }

}
.btn_back{
  width: 120px;
  height: 40px;
  background-color:#bae8e8;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  position: absolute;
  right: 40px;
  top: 50%;
  transform: translateY(-50%);
  font-weight: bold;
  text-transform: uppercase;
  font-size: 18px;
  color: #2c698d;
  &:hover{
    filter: opacity(0.6);
  }
}
footer{
  height: 50px;
  width: 100%;
  background-color: #2c698d;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: #272643;
  text-transform: uppercase;
  text-shadow: 2px 2px 4px #bae8e8;
}
footer span{
  margin-right: 40px;
}
.loader{
  width: 200px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  background-color: green;
  color: black;
}
.wrapper{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
  gap: 20px;
  margin: 0 40px;
}
.content{
  width: 100%;
  min-height: calc(100vh - 200px);
  padding: 100px 0 0 0;
  background-color: #e3f6f5;
  margin: 0 auto;
  padding-bottom: 50px;
}
.card{
  width: 100%;
  height: 330px;
  position: relative;
  border: 1px solid #2c698d;
  border-radius: 4px;
  box-shadow: 4px 4px 4px #272643;
  &__img{
    width: 100%;
    height: 200px;
    margin-top: 20px;
  }
  &__img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.card h4{
  text-align: center;
  color:#2c698d;
  text-transform: uppercase;
  text-decoration: none;
  &:hover{
    filter: opacity(0.4);
  }
}
.btn{
  display: block;
  width: 240px;
  height: 40px;
  margin: 0 auto;
  cursor: pointer;
  color: #272643;
  text-shadow: 2px 2px 4px #bae8e8;
  font-size: 18px;
  background-color: #2c698d;
  border: none;
  border-radius: 4px;
  margin-top: 30px;
  &:hover{
    filter: opacity(0.6);
  }
}
.card_descr{
  position: absolute;
  top: -1%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 8;
  width: 300px;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: #272643;
  border-radius: 4px;
  color:#bae8e8;
  padding: 20px;
}
.card_descr button{
  margin-bottom: 10px;
}
.card_catgory img{
  margin-top: 0;
}
.card_catgory h4{
  margin-top: 15px;
  &:hover{
    filter: none;
  }
}
.card_catgory{
  height: 350px;
  position: relative;
}
.card_catgory .btn{
  margin: 0;
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}
.recipe_block{
  margin-top: 50px;
  width: 1100px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &__title{
    text-align: center;
    color:#2c698d;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 24px;
  }
  &__subtitle{
    text-transform: uppercase;
    color:#2c698d;
    margin-top: 20px;
  }
  &__subtitle span{
    color: #272643;
    text-transform: capitalize;
  }
  &__img{
    margin-top: 20px;
    width: 500px;
    height: 300px;
  }
  &__img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__descr{
    margin-top: 30px;
    color: #272643;
    font-size: 16px;
    line-height: 24px;
  }
  &__video h4{
    text-transform: uppercase;
    color:#2c698d;
    margin-top: 20px;
    text-align: center;
  }
  &__video iframe{
    margin-top: 10px;
    width: 500px;
    height: 300px;
  }
  &__table{
    width: 600px;
    text-align: center;
    margin-top: 20px;
    border: 1px solid #2c698d;
    border-radius: 4px;
    background-color: #2c698d;
    color:#bae8e8;
  }
  &__table td{
    border: 1px solid #bae8e8;
  }
  &__table th{
    color: #272643;
  }
}


